<template>
	<div id="editStaffInfor">
		<div class="page_title">
			<router-link :to="{path:'/shop/staff'}">
				<span>员工管理</span>
			</router-link>
			<span class="blue">></span>
			<span class="blue">编辑员工信息</span>
		</div>
		<div class="staffCard">
			<div class="form_title">编辑员工信息</div>
			<div class="from_card">
				<el-form ref="form" :model="form1" label-width="100px" size="mini" :rules="rules">
					<el-form-item label="工号">
						<span>{{form1.employee_id}}</span>
					</el-form-item>
					<el-form-item label="店铺名称">
						<span>{{form.shop_name}}</span>
					</el-form-item>
					<el-form-item label="职位" prop="duty">
						<el-select v-model="form1.duty" placeholder="职位">
							<el-option label="0" value="cunshang"></el-option>
							<el-option label="1" value="dage"></el-option>
						</el-select>
						<router-link :to="{path:'/staff/editDuty'}">
							<el-button class="add-btn">新增职位</el-button>
						</router-link>
					</el-form-item>
					<el-form-item label="部门" prop="department_id">
						<el-select v-model="form1.department_id" placeholder="部门">
							<el-option v-for="item in allDepartName" :label="item" :value="item" :key="item"></el-option>
						</el-select>
						<el-button class="add-btn" @click="addNewDepartment">新增部门</el-button>
					</el-form-item>
					<el-form-item label="手机号">
						<span>{{form2.phone}}</span>
					</el-form-item>
					<el-form-item label="姓名">
						<span>{{form2.username}}</span>
					</el-form-item>
					<el-form-item label="身份证">
						<span>{{form2.identity}}</span>
					</el-form-item>
					<el-form-item label="性别">
						<span>{{form2.sex}}</span>
					</el-form-item>
					<el-form-item label="健康证扫描件">
						<img class="img" src="../image/emptypic_pic_normal.png" />
					</el-form-item>
					<el-form-item label="绑定微信">
						<span>{{form2.is_weixin}}</span>
					</el-form-item>
					<el-form-item>
						<el-button class="saveBtn" @click="saveEditStaff('form')">保存</el-button>
						<el-button class="cancelBtn" @click="cancelEditStaff('form')">取消</el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>
		<!--<new-duty v-if="isAddNewDuty"></new-duty>-->
		<new-department :isShowDialog="isAddNewDepartment" @on-close="hideDialog" :allDepartName="allDepartName"></new-department>
	</div>
</template>

<script>
	import { staffDataGet } from "../../api";
	import { treeMethods } from "../../index";
	//	import NewDuty from "./children/addNewDuty";
	import newDepartment from "./children/addNewDepartment";
	export default {
		data() {
			return {
				allDepartName: [],
				form1: {},
				form2: {},
				form: {},
				isAddNewDuty: true,
				isAddNewDepartment: false,
				rules: {
					duty: [{
						required: true,
						message: '请选择职位',
						trigger: 'change'
					}],
					department_id: [{
						required: true,
						message: '请选择部门',
						trigger: 'change'
					}]
				}
			}
		},
		components: {
			newDepartment
		},
		created() {
			this.initData();
			this.getAllDepartName();
		},
		methods: {
			//初始化表单数据
			initData() {
				this.employee_id = this.$route.query.employeeId
				let data = {
					get_employee_info: 1,
					employee_id: this.employee_id
				}
				staffDataGet.getStaffList(data, (resp) => {
					this.form = resp.data;
					this.form1 = resp.data.employee_info;
					console.log(this.form)
					this.form2 = resp.data.userinfo;
				})
			},
			//点击保存
			saveEditStaff(formName) {
				this.$refs[formName].validate(valid => {
					if(valid) {
						let data = {
							shop_employee_save: 1,
							employee_id: this.form1.employee_id,
							department_id: this.form1.department_id,
							duty_id: this.form1.duty,
						}
						staffDataGet.getUserInfor(data, (resp) => {
							this.goBack();
						})
					}
				})
			},

			//点击添加新部门
			addNewDepartment() {
				this.isAddNewDepartment = true;
			},
			//获取部门列表
			getAllDepartName() {
				let data = {
					get_department_list: 1
				}
				staffDataGet.getDepartList(data, (resp) => {
					resp.data.department_list.forEach((item) => {
						if(this.allDepartName.indexOf(item.department_name) == -1) {
							this.allDepartName.push(item.department_name)
						}
					})
				})
			},
			//获取职位列表
			//点击取消
			cancelEditStaff(formName) {
				this.$refs[formName].resetFields();
				this.goBack()
			},
			//隐藏弹框
			hideDialog(data) {
				this.allDepartName.push(data);
				this.isAddNewDepartment = false;

			},
			//返回员工管理页面
			goBack() {
				this.$router.push("/shop/staff")
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	.staffCard {
		background: #ffffff;
		box-shadow: 0 2px 4px 0 #becaeb;
	}
	
	.from_card {
		width: 50%;
		padding: 48px 0 40px 0;
		margin-left: 133px;
	}
	
	.page_title {
		@include wh(100%, 16px);
		@include fc(12px, #333333);
		margin-bottom: 14px;
	}
	
	.form_title {
		@include wh(100%, 40px);
		@include fc(14px, #666666);
		line-height: 40px;
		background-color: #F6F8FC;
		padding-left: 14px;
	}
	
	.img {
		width: 100px;
		height: 50px;
	}
	
	.pinpo {
		font-size: 12px;
		color: #9B9B9B;
	}
	
	.add-btn {
		margin-left: 14px;
	}
	
	.blue {
		font-size: 12px;
		color: #4877E7;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#editStaffInfor {
		.el-button {
			@include wh(80px, 30px);
			border: 1px solid #4877e7;
			color: #4877e7;
			border-radius: 5px;
			font-size: 12px;
		}
		.el-button.saveBtn {
			@include wh(100px, 34px);
			@include fc(14px, #ffffff);
			margin: 0 20px 0 300px;
			border: none;
			background: #4877e7;
		}
		.cancelBtn.el-button {
			@include wh(100px, 34px);
			@include fc(14px, #4877e7);
		}
	}
</style>


